@import url("https://fonts.cdnfonts.com/css/hk-groteks");

@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
  --backgroundTheme: #000;
}

* {
  margin: 0px;
  font-family: "HK Grotesk", sans-serif;
}

html{
  background: var(--backgroundTheme)
}

select {
  border-width: 0 !important;
  outline: 0 !important;
}

/*Scrollbar*/
::-webkit-scrollbar {
  width: 0px;
  border-radius: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #1f1f1f;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #1f1f1f;
}

*:focus {
  outline: none;
}
